import { Button } from 'antd';
import { deleteUser, getUser } from 'api/user';
import Loading from 'components/loading';
import TableCustom from 'components/TableCustom';
import React from 'react';
import { useQuery } from 'react-query';
import styles from './style.module.scss';

function User() {
  const { data, isFetching, refetch } = useQuery('getUser', () => getUser());

  const handleDeleteUser = async (id: number) => {
    try {
      await deleteUser(id);
      refetch();
    } catch (error) {}
  };
  const columns = [
    {
      title: 'Title',
      dataIndex: 'username',
      key: 'username',
    },

    {
      title: 'Action',
      dataIndex: 'action',
      key: 'action',
      render: (action: any, record: any) => {
        return (
          <div key={record?.id} className="color-blue font-medium cursor-pointer break-word">
            <Button>Sửa</Button>
            <Button onClick={() => handleDeleteUser(record?.id)}>Xóa</Button>
          </div>
        );
      },
    },
  ];

  if (isFetching) {
    return <Loading />;
  }
  return (
    <div className="manager">
      <div className="container-manager">
        <div className={styles.postContainer}>
          <div className={styles.addUser}>
            <Button className={styles.add}>Thêm người dùng</Button>
          </div>
          <TableCustom dataSource={data} columns={columns} />
        </div>
      </div>
    </div>
  );
}

export default User;
